<template><div><h1 id="java-io-nio" tabindex="-1"><a class="header-anchor" href="#java-io-nio"><span>Java IO/NIO</span></a></h1>
<h2 id="io-概述" tabindex="-1"><a class="header-anchor" href="#io-概述"><span>IO 概述</span></a></h2>
<p>Java IO（Input/Output）是 Java 中用于处理输入输出的 API。它主要分为：</p>
<ul>
<li>字节流（Byte Streams）</li>
<li>字符流（Character Streams）</li>
<li>缓冲流（Buffered Streams）</li>
<li>数据流（Data Streams）</li>
<li>对象流（Object Streams）</li>
</ul>
